.instruction {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  padding: 0 35px 61px 35px;
  box-sizing: border-box;
  font-family: 'Actay', sans-serif;
  animation: fadeOut 0.5s ease;
}
.instruction__content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  opacity: 1;
  transition: opacity 0.3s ease;
  position: relative;
  align-items: center;
}
.instruction__content.faded {
  opacity: 0;
}
.instruction__text {
  color: #348ff3;
  font-size: 20px;
  font-weight: 700;
  line-height: 25px;
  letter-spacing: -0.4099999964237213px;
  text-align: left;
  margin: 0;
  width: 287px;
  font-family: 'Actay Wide', sans-serif;
}
.instruction__text_colored {
  color: #ffbe30;
}
.instruction__text-tip {
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  width: 287px;
  color: #348ff3;
}
.instruction__link-box {
  display: flex;
  flex-direction: column;
  margin: 15px auto 15px auto;
}
.instruction__gallery {
  margin: 34px 0 34px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.instruction__step-image {
  z-index: 1;
}
.instruction__gallery-row {
  position: relative;
  display: flex;
  flex-wrap: nowrap;
}
.instruction__gallery-row.first {
  height: 174px;
}
.instruction__gallery-row.second {
  height: 176px;
}
.instruction__step-tooltip {
  font-size: 14px;
  line-height: 20px;
  font-weight: 700;
  color: #348ff3;
  position: absolute;
}
.instruction__step-rectangle {
  border: 1px solid #ffbe30;
  border-radius: 2.12811px;
  z-index: 2;
  position: absolute;
}
