  left: 30%;
}
.instruction__step-rectangle.one::before {
  content: '';
  position: absolute;
  width: 118px;
  height: 1px;
  background-color: #ffbe30;
  bottom: 5%;
  right: 100%;
  z-index: 2;
}
.instruction__step-rectangle.one::after {
  z-index: 2;
  content: '';
  width: 3px;
  height: 3px;
  position: absolute;
  background-color: #ffbe30;
  border-radius: 50%;
  right: 135px;
