.instruction__button-container {
  margin-top: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  position: relative;
}
.instruction__title {
  color: #348ff3;
  text-align: left;
  width: 287px;
  margin: 20% 0 18px 0;
  font-size: 32px;
  line-height: 34px;
  font-weight: 700;
  font-family: 'Actay Wide', sans-serif;
}
.instruction__button-top {
  height: 60px;
  box-sizing: border-box;
  width: 100%;
  position: relative;
  text-align: start;
  padding-left: 20px;
  font-size: 12px;
  color: #348ff3;
  display: flex;
  align-items: center;
  line-height: 0;
  background-color: transparent;
  border: transparent;
  margin-bottom: 20px;
  transition: all 0.3s linear;
  pointer-events: all;
}
.instruction__button-top.hidden {
  pointer-events: none;
  opacity: 0;
}
.instruction__button-corner {
  width: 10px;
  height: 10px;
  transform: rotate(-45deg);
  background-color: transparent;
  border-top: 2px solid #348ff3;
  border-left: 2px solid #348ff3;
  position: absolute;
  left: 0;
  transition: all 0.3s linear;
}

@keyframes fadeOut {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}
