.app-button {
  box-sizing: border-box;
  width: 90%;
  min-height: 48px;
  border-radius: 12px;
  cursor: pointer;
  box-shadow: 0px 4px 4px 0px #00000040;
  margin-top: 14px;
  font-size: 20px;
  line-height: 25px;
  padding: 0;
  font-weight: 700;
  font-family: 'Actay Wide', sans-serif;
}
.app-button-intro-primary {
  background-color: transparent;
  color: #348ff3;
  border: 1px solid #348ff3;
}
.app-button-intro-secondary {
  background-color: #348ff3;
  color: #fff;
  border: 1px solid #348ff3;
}
.app-button-gift {
  color: #ff824b;
  background-color: #fff;
  border-color: transparent;
}
.app-button-popup-gift {
  background-color: #ff824b;
  color: #fff;
  width: 90%;
  margin: 20px auto 0 auto;
  border-color: transparent;
}
.app-button-instruction-primary {
  background-color: #348ff3;
  color: #fff;
  border: 1px solid #348ff3;
  position: relative;
  z-index: 3;
}
.app-button-instruction-secondary {
  background-color: transparent;
  color: #348ff3;
  border: 1px solid #348ff3;
  position: relative;
  z-index: 1;
  opacity: 0;
  pointer-events: none;
  transform: translateY(64px);
  transition: all 0.3s ease;
  position: absolute;
  bottom: 64px;
}
.app-button-instruction-secondary.active {
  position: absolute;
  opacity: 1;
  pointer-events: all;
  transform: translateY(0);
}
.app-button-referral {
  color: #7472eb;
  background-color: #fff;
  border: 1px solid transparent;
}
.app-button-traffic-primary {
  width: 100%;
  border-color: transparent;
  color: #f95273;
  margin-bottom: 15px;
}
.app-button-traffic {
  border: 1.5px solid #ffffff;
  background-color: transparent;
  color: #fff;
  font-size: 14px;
  margin: 0;
  width: 48%;
}
.app-button.app-button-support {
  width: 100%;
  color: #348ff3;
  border-color: transparent;
  margin: 107px 0 0 0;
}
.app-button-values {
  color: #ff824b;
  border-color: transparent;
  font-size: 14px;
  width: 80%;
  margin: auto auto 45px auto;
}
.app-button-possibilities {
  color: #f95273;
  border-color: transparent;
  font-size: 14px;
  width: 80%;
  margin: auto auto 45px auto;
}
.app-button-faq {
  color: #7472eb;
  border-color: transparent;
  font-size: 14px;
  width: 80%;
  margin: auto auto 45px auto;
}
.app-button-news {
  color: #348ff3;
  border-color: transparent;
  width: 100%;
}
.app-button-popup-referral {
  color: #fff;
  background-color: #7472eb;
  border-color: transparent;
  margin: 20px auto 0 auto;
}
.app-button-popup-tariff {
  color: #fff;
  background-color: #348ff3;
  border-color: transparent;
  margin: 20px auto 0 auto;
}
.app-button-tariff-gift {
  width: 48%;
  border: 2px solid #348ff3;
  color: #348ff3;
  background-color: transparent;
}
.app-button-tariff-choose {
  width: 48%;
  border: 2px solid #348ff3;
  color: #fff;
  background-color: #348ff3;
}
.app-button-subscription_main {
  font-size: 14px;
  color: #064b72;
  width: 100%;
  border-color: transparent;
  box-shadow: none;
}
.app-button-subscription_secondary {
  font-size: 14px;
  width: 100%;
  color: #fff;
  border: 1.5px solid #fff;
  background-color: transparent;
  box-shadow: none;
}
.app-button.free {
  border-color: transparent;
  background-color: #7472eb;
  color: #fff;
}
.app-button.fit {
  margin-top: 0;
  color: #fff;
  background-color: #ff824b;
  border-color: transparent;
}
.app-button.nolimit {
  margin-top: 0;
  color: #fff;
  background-color: #f95273;
  border-color: transparent;
}
